const Jumbo = (props) => {
    return ( 
        <div className="jumbo" style={{
            background:
            `linear-gradient(to bottom, rgba(0,0,0,0)
            39%, rgba(0, 0, 0, 0.65) 100%),
            url('${props.image}'), #1c1c1c`
        }}>
                <div style={{ position: 'absolute', maxWidth: '1200px', bottom: '2rem', marginLeft: '10%'}}>
                    <h1 className="jumbo-heading" style={{color: 'white'}}>{props.title}</h1>
                    <p className="jumbo-para">{props.overview}</p>
                    <p>
                    <button className="jumbo-button" type="submit">View</button>
                    </p>
                </div>
        </div>
    );
}

export default Jumbo;