import React, {useEffect, useState} from "react";
import Jumbo from "./Components/Jumbo";
import { HiSearch } from 'react-icons/hi'
import { Link } from "react-router-dom";

import Movie from './Components/Movie'
import Footer from "./Components/Footer";

const FEATURED_API = "https://api.themoviedb.org/3/discover/movie?sort_by=popularity.desc&api_key=ce7af241c4351bc2b9e464ec9c9bbcc7&page=";
const LATEST_API = "https://api.themoviedb.org/3/discover/movie?sort_by=latest.desc&api_key=ce7af241c4351bc2b9e464ec9c9bbcc7&page="
const SEARCH_API = "https://api.themoviedb.org/3/search/movie?&api_key=ce7af241c4351bc2b9e464ec9c9bbcc7&query=";
const IMG_URL = "http://image.tmdb.org/t/p/";

const Landingpage = () => {
    const [movies, setMovies] = useState([]);
    const [searchTerm, setSearchTerm] = useState('');
    const [currentPage, setCurrentPage] = useState(0);
    const [currentAPI, setCurrentAPI] = useState('');

    useEffect(() => {
        getMovies(FEATURED_API+'1');
    },[]);

    const getMovies = (API) => {
        fetch(API)
        .then((res) => res.json())
        .then((data) => {
            setMovies(data.results);
            console.log(data.results);
            setCurrentPage(data.page);
            setCurrentAPI(API.substring(0,API.length-1));
        });
    }

    
    const handleOnSubmit = (e) => {
        e.preventDefault();
        
        if(searchTerm) {
            getMovies(SEARCH_API + searchTerm);
            setSearchTerm('');
        }
    };
    
    const handleOnChange = (e) => {
        setSearchTerm(e.target.value);
    };
    
    
    const handlePopular = (e) => {
        getMovies(FEATURED_API+'1');
    }
        
    const handleLatest = (e) => {
        getMovies(LATEST_API+'1');
    }

    const handleReload = () => {
        const endPoint = `${currentAPI}1`;
        getMovies(endPoint);
    }

    const handleNextPage = () => {
        const endPoint = `${currentAPI}${currentPage + 1}`;
        getMovies(endPoint);
    }

    const scrollToTop = () => {
        window.scrollTo({top: 500, behavior: "smooth"})
    }
        
    return ( 
        <>
        <header>
            <div className="nav-container">
                <div className="link-area">
                    <Link className="brand" to="/">
                        <div className="brand">
                            <h1>worldwidemovie.org</h1>
                        </div>
                    </Link>
                </div>
                <div className="search-area">
                    <form onSubmit={handleOnSubmit}>
                        <input className="search" type="search" placeholder="Search movie..." 
                            value={searchTerm} onChange={handleOnChange}/>
                            <button className="searchButton" type="submit"><HiSearch /></button>
                    </form>
                </div>
            </div>
        </header>
        
        {movies[0] &&
            <Jumbo 
            image={`${IMG_URL}w1280${movies[0].backdrop_path}`} 
            title={movies[0].original_title}
            overview={movies[0].overview}/>
        }
        

        <div className="sort-movies">
            <button onClick={handlePopular} className="popular-button sort-button" type="submit">Popular</button>
            <button onClick={handleLatest} className="latest-button sort-button" type="submit">Latest</button>
        </div>
        
        <div className="page">
            <div className="movie-container">
            {movies && movies.map((movie) =>
                <Movie key={movie.id} {...movie}  />)}
            </div>
        </div>

        <div style={{display: 'flex', justifyContent: 'center', marginTop: '50px'}}>
            <button onClick={() => {handleReload(); scrollToTop()}}>Reload</button>
            <button onClick={() => {handleNextPage(); scrollToTop()}}>Next Page</button>
        </div>
        
        <Footer />

    </>
    );
}

export default Landingpage;