import React, { useEffect } from "react";

import { useParams } from "react-router-dom";


const MovieDetailPage = (props) => {

    const { movieId } = useParams();

    useEffect((props) => {

        const MOV_API = `https://api.themoviedb.org/3/movie/${movieId}?api_key=ce7af241c4351bc2b9e464ec9c9bbcc7&language=en-US`

        fetch(MOV_API)
        .then(response => response.json())
        .then(response => {
            console.log(response);
        })

    }, [])

    return ( 
        <div>
            <h1 style={{color: 'white'}}>This is movie details page with id {movieId}.</h1>
        </div>
    )
}

export default MovieDetailPage;