import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Landingpage from "./Landingpage";
import MovieDetailPage from "./MovieDetailPage";

function App() {
  return ( 
  <>
  <Router>
    <div className="web-app">
      <Switch>
        <Route exact path="/">
          <Landingpage />
        </Route>
        <Route path="/MovieDetailPage/:movieId">
          <MovieDetailPage />
        </Route>
      </Switch>
    </div>
  </Router>
  </>
  );
}

export default App;
